@font-face {
  font-family: 'Barlow';
  src: local('Barlow'), url(./fonts/Barlow/Barlow-Regular.ttf) format('truetype');
}
@font-face {
  font-family: 'Barlow';
  font-weight: 700;
  src: local('Barlow'), url(./fonts/Barlow/Barlow-Bold.ttf) format('truetype');
}
@font-face {
  font-family: 'Barlow';
  font-weight: 300;
  src: local('Barlow'), url(./fonts/Barlow/Barlow-Light.ttf) format('truetype');
}
@font-face {
  font-family: 'Roboto';
  src: local('Roboto'), url(./fonts/Roboto/Roboto-Regular.ttf) format('truetype');
}
@font-face {
  font-family: 'Roboto';
  font-weight: 700;
  src: local('Roboto'), url(./fonts/Roboto/Roboto-Bold.ttf) format('truetype');
}
@font-face {
  font-family: 'Roboto';
  font-weight: 300;
  src: local('Roboto'), url(./fonts/Barlow/Barlow-Light.ttf) format('truetype');
}
body {
  background-color: gainsboro;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: medium;
  color: #41403d
}


h1 {
  font-family: Barlow, sans-serif;
  font-size: 70px;
}
h2, h3 {
  font-family: Barlow, sans-serif;
}

.longString {
  overflow-x: scroll;
  white-space: nowrap;
}




